.testimonials {

  &__container {
    @include content-padding;
  }

  &__label {
    font-family: Poppins, sans-serif;
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    color: #C0CDD7;
    margin-bottom: 16px;
  }

  &__title {
    font-family: Poppins, sans-serif;
    font-size: 32px;
    font-weight: 600;
    line-height: 48px;
    text-align: left;
    color: #253757;
    margin-bottom: 48px;
  }

  &__info {
    @include page-grid;
  }

  &__card {
    display: flex;
    flex-direction: column;
    align-items: center;

    box-sizing: border-box;
    padding: 32px 40px;

    width: 100%;
    height: auto;

    border-radius: 16px ;
    background-color: #FFF;
    box-shadow: 0 9px 18px 0 #25293108;

    margin-bottom: 20px;

    grid-column: 1/-1;

    @include on-tablet {
      margin-bottom: 30px;
      grid-column: span 6;
    }

    @include on-small-desktop {
      grid-column: span 4;
    }

    @include hover(transform,scale(1.1));
  }

  &__card__photo {
    width: 196px;
    height: 196px;

    margin-bottom: 24px;

    @include on-tablet {
      width: 250px;
      height: 250px;
    }
  }

  &__card__scobs {
    margin-bottom: 11px;
  }

  &__card__text {
  font-family: 'Open Sans', sans-serif;
  font-size: 13px;
  font-weight: 600;
  line-height: 19.5px;
  text-align: center;
  color: $article-color;
  margin-bottom: 16px;
  }

  &__card__name {
    font-family: 'Open Sans', sans-serif;
    font-size: 13px;
    font-weight: 600;
    line-height: 19.5px;
    text-align: center;
    color: $title-color;
    margin-bottom: 4px;
  }

  &__card__prof {
    font-family: Poppins, sans-serif;
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    text-align: center;
    text-transform: uppercase;
    color: $article-color;
  }

}
