.header {
  background-color: #2c2c2c;
  border-bottom-right-radius: 32px;
  border-bottom-left-radius: 32px;
  height: 100vh;
}

.header__container {
  padding-inline: 20px;
}

@media (width >= 640px) {
  .header__container {
    padding-inline: 41px;
  }
}

@media (width >= 1024px) {
  .header__container {
    padding-inline: 54px;
  }
}

@media (width >= 1280px) {
  .header__container {
    padding-inline: 227px;
  }
}

.header__content {
  flex-direction: column;
  padding-top: 20px;
  display: flex;
}

@media (width >= 1024px) {
  .header__content {
    padding-top: 49px;
  }
}

.header__top {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__menu {
  align-items: center;
  display: flex;
}

.header__link {
  margin-right: 32px;
  display: block;
}

.header__link--icon {
  background-image: url("icon-burger.82261b9a.png");
  background-repeat: no-repeat;
  width: 18px;
  height: 8px;
  transition: transform .3s;
}

@media (width >= 1024px) {
  .header__link--icon {
    display: none;
  }
}

.header__link--icon:hover {
  transform: scale(1.2);
}

.header__hire__us {
  color: #fff;
  letter-spacing: 2px;
  text-align: center;
  text-transform: uppercase;
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 20px;
  text-decoration: none;
  transition: transform .3s;
  display: block;
  position: relative;
}

.header__hire__us:hover {
  transform: scale(1.2);
}

.header__hire__us:before {
  content: "";
  background-color: #2060f6;
  width: 100%;
  height: 2px;
  display: block;
  position: absolute;
  bottom: -8px;
}

.header__hire__us:hover {
  text-shadow: 1px 1px 2px red, 0 0 1em #00f, 0 0 .2em #00f;
}

.header__title {
  box-sizing: border-box;
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  margin-top: 48px;
  display: grid;
}

@media (width >= 640px) {
  .header__title {
    --columns: 6;
    column-gap: 24px;
  }
}

@media (width >= 1024px) {
  .header__title {
    --columns: 12;
